@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500;600&display=swap);

/* =========== google fonts =========== */

/* ======== base style ======= */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body,
html {
  /* background: #0a071a; */
}

section,
footer {
  padding: 70px 0px;
}

h1,
h2 {
  font-size: 2rem;
  font-weight: 500;
  font-family: "Poppins", sans-serif;
  color: #f5b70a;
}

p {
  font-size: 1rem;
  color: #c4c4c4;
  line-height: 30px;
  font-family: "Rubik", sans-serif;
}

a {
  text-decoration: none;
  color: unset;
}

i {
  color: #fff;
}

.header{width:100%;height:80px;line-height:80px;position:fixed;top:0;left:0;z-index:9;background:#eeedf2}.navigation{display:flex;align-items:center;justify-content:space-between}.logo h2{margin-bottom:0}.nav__list{display:flex;align-items:center;margin-bottom:0;list-style:none;grid-column-gap:2.7rem;column-gap:2.7rem}.nav__item a{color:#f76e11;cursor:pointer;transition:.3s}.nav__item a:hover{color:#f5b70a}.custom__search{display:flex;align-items:center;padding:10px 25px;height:35px;border-radius:50px;background:rgba(196,196,196,.1450980392)}.custom__search input{border:none;outline:none;background:rgba(0,0,0,0);color:#f76e11}.custom__search span i{cursor:pointer}.mobile__menu{display:none}.mobile__menu span i{font-size:1.3rem}.cart__icon{position:relative;z-index:11;width:40px;height:30px;border-radius:5px;background:#f76e11;display:flex;align-items:center;justify-content:center}.badge{position:absolute;top:-2px;right:-3px;z-index:12;color:#f3eeee;font-size:.8rem;font-weight:700}
